@import "../../var.css";

.contact-container {
  width: 100%;
  height: 400px;
  background-color: var(--bg1-color);
  padding: 30px;
  overflow: hidden;
  justify-items: center;
}
.contact-content-container {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 30px 0 0 0;
  width: 84%;
  height: 70%;
  background-color: var(--bg2-color);
  border-radius: 10px;
}

.contact-heading {
  text-align: center;
  font-family: var(--heading-font);
  color: var(--button-bg-color);
  font-size: 44px;
}
.content {
  width: 50%;
  height: 90%;
  justify-items: center;
  align-content: center;
  border-right: 2px solid var(--text-color);
}
.contact-para {
  font-family: var(--text-font);
  color: var(--text-color);
  font-size: 50px;
  width: 60%;
}
.links {
  align-items: center;
  width: 50%;
  display: flex;
  flex-direction: column;
  gap: 10px;
}
.links a {
  text-decoration: none;
  color: var(--text-color);
  width: 200px;
  text-align: left;
  padding: 5px 8px;
  position: relative;
  background-color: var(--bg1-color);
  border-radius: 5px;
  transition: 0.5s ease;
}
.links span {
  justify-self: center;
  position: absolute;
  font-family: var(--text-font);
  top: 23%;
  left: 25%;
  font-size: 16px;
}

.links a:hover {
  transform: scale(1.1);
  color: var(--button-bg-color);
}

@media (max-width: 1125px) {
  .contact-para {
    font-size: 45px;
  }
}
@media (max-width: 805px) {
  .contact-para {
    font-size: 40px;
  }
}
@media (max-width: 720px) {
  .contact-para {
    font-size: 38px;
  }
}
@media (max-width: 575px) {
  .contact-para {
    font-size: 25px;
    width: auto;
    align-content: center;
    text-align: center;
    margin-top: 5px;
  }
  .content {
    border: none;
    width: 100%;
    height: 20%;
  }

  .contact-content-container {
    flex-direction: column;
    height: 80%;
    padding: 0 30px;
  }
  .links {
    flex-direction: row;
    flex-wrap: wrap;
    height: 80%;
    width: 100%;
    justify-content: center;
    padding: 15px 0;
  }
  .links a {
    width: 150px;
  }
  .links span {
    left: 30%;
  }
}
