@import "../../var.css";

.projectcontainer {
  width: 100%;
  height: 100vh;
  background-color: var(--bg1-color);
  justify-items: center;
  padding: 60px 0 0 0;
}
.project-heading {
  font-family: var(--heading-font);
  font-size: 44px;
  color: var(--button-bg-color);
  margin-bottom: 30px;
}
.swipercontainer {
  width: 80%;
  height: 80%;
  background-color: var(--bg2-color);
  border-radius: 10px;
  align-content: center;
  overflow: hidden;
}
.swiper {
  overflow: visible !important;
}

.slides {
  position: relative;
  width: 300px;
  height: 370px;
  background-color: var(--bg2-color);
  padding: 10px;
  border-radius: 10px;
  justify-self: center;
  overflow: hidden;
  transition: background-color 0.3s ease;
  box-shadow: 0px 0px 10px 0px var(--button-text-color);
}
.slides::after {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--card-overlay-color);
  opacity: 0;
  transition: opacity 0.3s ease;
  border-radius: inherit;
  z-index: 1;
}

.slides .overlay-btn {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  background: #fff;
  color: #333;
  padding: 10px 20px;
  border-radius: 8px;
  border: none;
  cursor: pointer;
  font-weight: bold;
  opacity: 0;
  pointer-events: none;
  transition: all 0.3s ease;
}

.slides:hover::after {
  opacity: 1;
}
.slides:hover .overlay-btn {
  opacity: 1;
  pointer-events: auto;
}
.slides .overlay-btn:hover {
  background: var(--button-bg-color);
  color: var(--button-text-color);
}

.swiper-button-next,
.swiper-button-prev {
  color: var(--button-text-color) !important;
}

.imageholder {
  height: 45%;
  width: 100%;
  border: 1px solid black;
  border-radius: 5px;
  overflow: hidden;
  position: relative;
}
.projectimage {
  width: 100%;
  height: 100%;
  object-fit: fill;
}
.lmsimage {
  height: 100%;
}
.projectname {
  font-family: var(--heading-font);
  font-size: 22px;
  font-weight: bolder;
  color: rgb(18, 179, 197);
  text-align: center;
  margin: 5px 0 5px 0;
}
.projectdescription {
  margin-left: 5px;
  font-size: 14px;
  font-family: var(--text-font);
  color: var(--text-color);
}

@media (max-width: 430px) {
}
@media (max-width: 380px) {
  .slides {
    width: 250px;
    height: 450px;
  }
}
