@import "../../var.css";

.header {
  display: flex;
  width: 100%;
  height: 60px;
  color: var(--text-color);
  text-align: center;
  align-content: center;
  justify-content: space-between;
  padding: 0.8em 0.9em 0 10%;
  font-size: 24px;
  position: fixed;
  z-index: 5;
  border-bottom: 1px solid var(--bg2-color);
}

.header.solid {
  background-color: var(--bg1-color);
}

.header.blurred {
  background-color: calc(var(--bg1-color) + rgba(0.5));
  backdrop-filter: blur(10px);
}

.logo {
  position: relative;
  top: -9px;
}

.logo a {
  font-family: var(--heading-font);
  font-weight: bold;
  font-size: 30px;
  text-decoration: none;
  color: var(--text-color);
}
#navbar {
  display: flex;
}
.ul {
  list-style: none;
  display: flex;
  gap: 0px;
  font-size: 16px;
  position: relative;
  top: -3px;
}
.menuicon {
  visibility: hidden;
  cursor: pointer;
  opacity: 0;
}
.closeicon {
  visibility: hidden;
  cursor: pointer;
  opacity: 0;
}
.ul li {
  padding: 3px 20px;
}
.ul li a {
  font-family: var(--heading-font);
  font-weight: 600;
  text-decoration: none;
  color: var(--text-color);
  font-size: 16px;
  transition: 800ms ease;
}
.ul li a:hover {
  color: var(--frame-bg-color);
  border-radius: 3px;
}

#btn {
  top: -7px;
  position: relative;
}
.download_btn {
  padding: 7px 10px;
  border-radius: 50px;
  background-color: var(--button-bg-color);
  font-size: 15px;
  transition: 800ms ease-in-out;
  cursor: pointer;
  text-decoration: none;
  font-family: var(--heading-font);
  font-weight: 600;
  color: white;
  border: none;
}

.download_btn:hover {
  color: var(--button-bg-color);
  background-color: white;
  transform: scale(1.05, 1.05);
}

@media (max-width: 865px) {
  .download_btn {
    font-size: 14px;
    text-wrap-mode: nowrap;
  }
}

@media screen and (max-width: 835px) {
  .header {
    padding-left: 10%;
  }
  .ul {
    flex-direction: column;
    gap: 15px;
  }
  .ul li a {
    font-size: 20px;
  }
  nav {
    position: fixed;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    justify-items: center;
    background: var(--bg2-color);
    padding: 15rem 0rem 0 0;
    transition: 1s;
    transform: translateY(-150vh);
    z-index: 2;
  }
  .responsive_nav {
    transform: none;
  }

  .menuicon,
  .closeicon {
    visibility: visible;
    opacity: 1;
  }
  .closeicon {
    position: fixed;
    top: 15px;
    right: 15px;
  }
  .menuicon {
    margin-left: 20px;
  }
}
