@import "@fontsource/montserrat";
@import "@fontsource/inter";

:root {
  --text-color: white;
  --bg2-color: rgb(11, 20, 40);
  /* --bg2-color: rgb(51, 59, 60); */
  --bg1-color: rgb(7, 12, 24);
  /* --bg1-color: rgb(35, 43, 44); */
  --heading-font: "montserrat", sans-serif;
  --text-font: "inter", sans-serif;
  --button-text-color: white;
  --button-bg-color: rgb(18, 197, 217);
  --button-bg-color-hover: rgb(147, 31, 147);
  --frame-bg-color: rgb(18, 197, 217);
  --hero-heading-color: var(--button-text-color);
  --cards-bg-color: rgb(193, 193, 193);
  --card-overlay-color: rgba(7, 12, 24, 0.9);
  --downarrow-color: rgb(84, 86, 86);
}
