@import "/src/var.css";

.skill-container {
  min-height: 110vh;
  justify-items: center;
  padding: 50px 0 0 0;
  background-color: var(--bg1-color);
}
#skill-heading {
  font-family: var(--heading-font);
  font-size: 44px;
  color: var(--button-bg-color);
  margin-bottom: 30px;
}
.skill-cards-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  justify-content: center;
  row-gap: 20px;
  align-items: center;
  width: 98vw;
  height: 80%;
  padding: 20px 60px;
  z-index: 0;
}
.skill-cards {
  width: 90%;
  aspect-ratio: 1.1/1;
  border: 2px solid var(--text-color);
  color: azure;
  border-radius: 10px;
  align-content: center;
  justify-items: center;
  text-align-last: center;
  position: relative;
  background-color: transparent;
  z-index: 0;
  overflow: hidden;

  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.skill-cards:hover {
  transform: scale(1.05);
  box-shadow: 0px 0px 20px 0px var(--button-bg-color);
  color: white;
}

.skill-cards::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border-radius: 5px;
  background-color: var(--bg-color);
  z-index: -1;
}

::after {
  box-sizing: border-box;
}
.skill-image {
  width: 70px;
  margin-bottom: 20px;
}
.skill-text {
  text-align: center;
  font-family: var(--text-font);
  font-size: 16px;
}

.animate {
  transform: translateX(0);
  opacity: 1;
}

@media (max-width: 780px) {
  .skill-cards-container {
    grid-template-columns: repeat(auto-fit, minmax(136px, 170px));
    column-gap: 3px;
  }
  .skill-cards {
    aspect-ratio: 1/1;
  }
  .skill-image {
    margin: 0 0 0 3px;
  }
  .skill-text {
    font-size: 16px;
  }
}

@media (max-width: 430px) {
  .skill-cards-container {
    grid-template-columns: repeat(auto-fit, minmax(110px, 1fr));
    padding: 0 20px 20px 30px;
  }

  .skill-image {
    width: 50px;
  }
}
