@import "../../var.css";
.footer {
  height: 40px;
  width: auto;
  align-items: center;
  background-color: var(--bg1-color);
  display: flex;
  justify-content: center;
  border-top: 2px solid var(--bg2-color);
  padding: 0 10px;
}
.footer p {
  color: var(--text-color);
  font-family: var(--text-font);
  font-size: 15px;
  text-align: center;
}

@media (max-width: 400px) {
  .footer p {
    font-size: 14px;
  }
}
@media (max-width: 380px) {
  .footer {
    height: 40px;
  }
}
