@import "../../var.css";

.container {
  background-color: var(--bg1-color);
  width: 100%;
  min-height: 110vh;
  display: flex;
  position: relative;
}
.textContainer,
.imageContainer {
  height: 100vh;
  width: 50%;
}
.textContainer {
  display: block;
  align-content: center;
  justify-items: center;
  padding-left: 10%;
}

.name {
  font-family: var(--heading-font);
  font-size: 36px;
  color: var(--button-bg-color);
  font-weight: bolder !important;
}
#hero-heading {
  width: 100%;
  margin-bottom: 18px;
  font-size: 32px;
  color: var(--text-color);
  font-family: var(--heading-font);
}
#hero-para {
  color: var(--text-color);
  font-size: 18px;
  position: relative;
  width: fit-content;
  padding-left: 3px;
  font-family: var(--text-font);
}
.contact_btn {
  font-family: var(--text-font);
  /* margin-top: 10px; */
  padding: 5px 14px;
  position: absolute;
  right: 40%;
  bottom: -50px;
  font-size: 18px;
  text-decoration: none;
  color: var(--button-text-color);
  background-color: var(--button-bg-color);
  border-radius: 30px;
  transition: 1s 0.1s;
  border-color: var(--button-bg-color);
  border-width: 3px;
  border-style: solid;
  z-index: 2;
}
.contact_btn:hover {
  box-shadow: 0px 0px 15px black;
  background-color: var(--button-bg-color);
  border-style: solid;
  transform: scale(1.05, 1.05);
}

.imageContainer {
  align-content: center;
  justify-items: center;
}
.frame {
  width: 250px;
  height: 300px;
  background-color: transparent;
  border-radius: 50%;
  border-color: var(--button-bg-color);
  border-style: double;
  border-width: 8px;
  background-image: url(/src/assets/MyPic.png);
  background-repeat: no-repeat;
  background-size: cover;
  background-position-x: 12px;
  background-position-y: center;
  box-shadow: 0px 0px 11px 0px var(--frame-bg-color);
}

.downarrow {
  position: absolute;
  bottom: 20px;
  right: 48%;
  color: var(--downarrow-color);
  transform-origin: center;
  transform: scale(3.5, 2.5);
  animation: animate 2s ease-in-out infinite;
  z-index: 1;
}

@keyframes animate {
  0% {
    bottom: 10px;
  }
  50% {
    bottom: 25px;
  }
  100% {
    bottom: 10px;
  }
}

@media (max-width: 800px) {
  .frame {
    width: 230px;
    height: 280px;
  }
  #hero-heading {
    font-size: 30px;
  }
  .name {
    font-size: 32px;
  }
  #hero-para {
    font-size: 16px;
  }
  .contact_btn {
    right: 30%;
  }
  .downarrow {
    right: 47%;
  }

  @keyframes animate {
    0% {
      bottom: 0px;
    }
    50% {
      bottom: 8px;
    }
    100% {
      bottom: 0px;
    }
  }
}
@media (max-width: 670px) {
  .container {
    flex-direction: column-reverse;
    height: 110vh;
    width: 100%;
  }
  .textContainer {
    padding: 0px 65px;
  }
  .imageContainer {
    height: 50%;
    width: 100%;
  }
  .textContainer {
    width: 100%;
    height: 60%;
    margin-top: -100px;
  }
  .frame {
    margin-top: 12%;
    height: 250px;
    width: 200px;
    background-size: 200px;
    background-position-x: 8px;
  }
  .downarrow {
    right: 45%;
  }
  .contact_btn {
    right: 33%;
  }

  @keyframes animate {
    0% {
      bottom: -13%;
    }
    50% {
      bottom: -15%;
    }
    100% {
      bottom: -13%;
    }
  }
}
@media (max-width: 500px) {
  .contact_btn {
    right: 30%;
  }
  .downarrow {
    right: 45%;
  }
}

@media (max-width: 430px) {
  #hero-heading {
    font-size: 26px;
  }
  .name {
    font-size: 30px;
  }
  #hero-para {
    font-size: 15px;
  }
  .contact_btn {
    font-size: 16px;
  }
  .imageContainer {
    height: 10%;
    padding-top: 0px;
  }
  .textContainer {
    height: 50%;
    padding-top: 0px;
  }
  .frame {
    height: 220px;
    width: 180px;
    background-position-x: 10px;
    background-size: cover;
    position: absolute;
    top: 11vh;
  }
  .downarrow {
    right: 44%;
  }
  .container {
    flex-direction: column-reverse;
    padding-top: 0;
  }
  .textContainer {
    align-content: baseline;
    padding: 0 30px;
  }
  .contact_btn {
    right: 27%;
  }
  @keyframes animate {
    0% {
      bottom: -12%;
    }
    50% {
      bottom: -14%;
    }
    100% {
      bottom: -12%;
    }
  }
}
